.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


/* Changes added to create-react-app output and reactdnd-tutorial by ZH */
/* ============================================================================== */
.App{
  background-color: #222;
  padding-bottom: 6em;
}

.App-header{
  display: -ms-flexbox;
  display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  height: 0%;
}

/* without a specific height and width, the board and squares will look collapsed and non-existent */
.App-main{
  border: solid #fff 2px;
  height: 400px;
  margin: 2em auto;
  width: 400px;
}

.App-link{
  color: #61DAFB;
  text-decoration: none;
}
.App-link:hover{
  color: #33FA6F;
  -webkit-transition: 0.3s linear;
  -o-transition: 0.3s linear;
  transition: 0.3s linear;
}



